/**
 *  vuex 
 */
import Vue from 'vue'
import Vuex from 'vuex'
// import config from '@/config'
import app from './app'
import user from './user'

Vue.use(Vuex)

//  https://vuex.vuejs.org/zh-cn/strict.html
const debug = process.env.NODE_ENV !== 'production'

// 
export default new Vuex.Store({
  modules: {
    app,
    user
  },
  strict: debug
})



